button {
  background: #509679;
  border: none;
  border-radius: 5px;
  color: #fff;
  height: 30px;
  margin: 0 20px 30px;
  width: 100px;
  -webkit-transition: background 0.5s;
  -o-transition: background 0.5s;
  transition: background 0.5s;
}

button:hover {
  background: #204938;
  -webkit-transition: background 0.5s;
  -o-transition: background 0.5s;
  transition: background 0.5s;
}

@media (max-width: 880px) {
  button {
    margin: 0 10px 30px;
  }
}


