.input {
  color: #000;
  font-size: 20px;
  height: 50px;
  width: 11%;
  -webkit-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s;
}

@media (max-width: 880px) {
  .input {
    height: 40px;
  }
}

@media (max-width: 600px), (max-height: 400px) {
  .input {
    height: 35px;
  }
}

.active {
  background: rgba(0, 0, 0, 0.5);
  border: none;
  color: #fff;
  -webkit-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s;
}

.targeted {
  background: rgba(80, 150, 121, 0.5);
  border: none;
  color: #fff;
  -webkit-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
 -moz-appearance: textfield;
}



